import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Task2Component } from './pages/task2/task2.component';
import { Task3Component } from './pages/task3/task3.component';
import { TransaccionsComponent } from './pages/transaccions/transaccions.component';

const routes: Routes = [
  {path: 'task2', component: Task2Component},
  {path: 'task3', component: Task3Component},
  {path: 'transaccion', component: TransaccionsComponent},
  {path: '**', pathMatch: "full", redirectTo: 'task2'}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
