import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private _http: HttpClient) { }

  getTransaccions(){
    return this._http.get('apiFake/v1/transaccions');
  }

  getUsers(){
    return this._http.get('apiFake/v1/users');
  }

  getAccounts(){
    return this._http.get('apiFake/v1/accounts');
  }
}
